<template>
	<div class="my-form">
		<p>Zalogowany jako: {{getUserName}}</p>
		<button @click="updateConfig">config</button>
		<button @click="createAlbum">pokabierz liste</button>
	</div>

</template>

<script type="text/javascript">
	import router from '../router';
	import {IdentityPoolId, bucketRegion, bucketName, poolData, region} from '../utils/env.js'
	import AWS from 'aws-sdk'

	export default {
		name: 'main',
		props: ['cognito'],
		data(){
			return{
				s3: {},
				items: [],
				keyID: 0
			}
		},
		computed: {
			getUserName(){
				return this.cognito.getUsername();
			}
		},
		methods:{
			listItems(){

				this.s3.listObjects({}, (err, results) =>{
		        if(err){
		            return alert (err)
		        }
		        results.Contents.forEach( item => {
		        	this.items.push(item.key)
		        })
			})
			},
			createAlbum(){
				let albumName = "kubasdasd"
				if (!albumName) {
			    return alert('Album names must contain at least one non-space character.');
			  }
			  if (albumName.indexOf('/') !== -1) {
			    return alert('Album names cannot contain slashes.');
			  }
			  var albumKey = encodeURIComponent(albumName) + '/';
			  this.s3.headObject({Key: albumKey}, function(err, data) {
			    if (!err) {
			      return alert('Album already exists.');
			    }
			    if (err.code !== 'NotFound') {
			      return alert('There was an error creating your album: ' + err.message);
			    }
			    this.s3.putObject({Key: albumKey}, function(err, data) {
			      if (err) {
			        return alert('There was an error creating your album: ' + err.message);
			      }
			      alert('Successfully created album.');
			      viewAlbum(albumName);
			    });
			  });
			},
			updateConfig(){
				// this.keyID = AWS.config.credentials.accessKeyId;

				AWS.config.update({
				  region: region,
				  credentials: new AWS.CognitoIdentityCredentials({
				    IdentityPoolId: IdentityPoolId
				  })
				});
				
				

				this.s3 = new AWS.S3({
					  apiVersion: '2006-03-01',
					  params: {Bucket: bucketName},
					  region: bucketRegion
					});



				// AWS.config.credentials.get(function(){

				//     // Credentials will be available when this function is called.
				//     var accessKeyId = AWS.config.credentials.accessKeyId;
				//     var secretAccessKey = AWS.config.credentials.secretAccessKey;
				//     var sessionToken = AWS.config.credentials.sessionToken;

				//     console.log(accessKeyId + " ------")
				//     console.log(secretAccessKey + " ------")
				//     console.log(sessionToken + " ------")

				// });
			}	


		},
		created(){
			AWS.config.update({
				  region,
				  credentials: new AWS.CognitoIdentityCredentials({
				    IdentityPoolId
				  })
				});
				
				
				this.s3 = new AWS.S3({
					  apiVersion: '2006-03-01',
					  params: {Bucket: bucketName},
					  region: bucketRegion
					});

		}
	};

</script>


